
#community {
    width: 100%; margin: 0px auto; padding-top: 0;

    .topPic {
        background: url(/public/img/slider11.jpg) center; width: 100%;
        height: 60vh; background-repeat: no-repeat; background-attachment: fixed;
        background-size: cover;

        p {
            position: absolute; font: bold 60px/1.8 "DM Serif Display"; letter-spacing: 3px;
            color: #fff; top: 30%; left: 60px; transform: translateY(-50%);
            
        }
        span {
            position: absolute; font: normal 25px "Cardo";
            color: #FFF;top: 37%; left: 60px;
        }

        aside{
            width: 800px; height: 100px; position: absolute; top: 31%; left: 30px;
            overflow: hidden; z-index: 3;

            .top {
                width: 60%;  height: 1px; background: #fff;
                position: absolute; top: 0; left: 400px;
            }
            .right {
                width: 1px; height: 100%; background: #fff;
                position: absolute; top: 0; right: 0;
            }
            .bottom {
                width: 100%; height: 1px; background: #fff;
                position: absolute; bottom: 0; right: 0;
            }
            .left {
                width: 1px; height: 100%; background: #fff;
                position: absolute; bottom: 0; left: 0;
            }
            .topLeft {
                width: 20px;
                height: 1px; background: #fff; position: absolute; top: 0; left: 0;
            }
        }
    }
    .inner {
        width: 1180px; height: 1100px; 
        position: relative; margin: 0 auto;
        
        >h1 {
            position: absolute;
            top: 100px;
            left: 0px;
            
            a {
                font: bold 40px/1.8 "Noto Serif Display";
                color: rgba(88,91,73,1); letter-spacing: 1px;
            }
        }
        >p {
            margin-top: 30px; position: absolute; top: 150px; left: 0px;
            width: 400px; font: normal 16px/1.8 "Baloo 2";
            font-weight: 300; text-align: left; letter-spacing: 0.5px;
        }
        .thead {
            width: 90%; position: absolute; top: 290px; left: 0px;
            background: #f2efeb;
            padding: 17px 0;

            .num {
                padding-left: 20px;
                color: #302c28; font-family: "Open Sans";
                font-size: 15px;
            }
            .title {
                padding-left: 40%;
                color: #302c28; font-family: "Open Sans";
                font-size: 15px; 
            }
        }
        article {
            float: left;  transform: translateY(350px);
            width: 90%; height: 50px;
            padding: 10px;
            border-bottom: 1px solid #ddd;
            vertical-align: middle;
            margin: 5px;

            span {
                font-size: 14px; color: #666666;
                font-family: 'Open Sans'; margin-left: 2%;
            }
            >h1 {
                font: 16px "Baloo 2"; transition: 0.5s;
                color: #666; cursor: pointer;
                padding-left: 4%; font-weight: 300;
                display: inline-block; letter-spacing: -0.5px; margin-bottom: 0;

                &:hover { color: rgba(220,198,175,1);}
            }
            a {
                font-size: 14px; color: #666666;
                font-family: 'Open Sans';
                position: absolute;
                right: 0; padding-right: 2%;

            }
        }
        .btns {
            display: block; position: absolute;
            bottom: 60px; width: 90%; 
            .search{
                float: left;
                width: 65px; height: 29px;
                line-height: 27px; text-align: center;
                border: 1px solid #ddd7d2;
                color: #938c85 !important;
                font-size: 12px; font-family: 'sans serif';
            }
            .sort {
                float: right;
                width: 65px; height: 29px;
                line-height: 27px; text-align: center;
                border: 1px solid #ddd7d2;
                color: #938c85 !important;
                font-size: 12px; font-family: 'sans serif';
            }

            
        }
        .pages {
            position: absolute; bottom: -30px; left: 50%; transform: translateX(-50%); 
            display: block; width: 300px; margin: 0 auto;

            li {
                float: left; padding: 6px 12px;

                a {  color: #666666; }

                &:nth-of-type(3) {
                    a {
                        color: #51402f !Important;
                        font-size: 18px;
                    }

                    &::after {
                        content: ""; display: block; margin-top: 5px;
                        width: 120%; height: 1px; background: #51402f;
                    }
                }
            }   
        }
    }
}
